.getallblog{
    width: 85%;
    min-height: 100vh;
    height: auto;
}







@media screen and (max-width:700px){
    .getallblog{
        width: 85%;
        min-height: 100vh;
        height: auto;
        margin-bottom: 10rem;
    }
    .getallblog .real{
        position: relative;
        top: 15vh;
        width: 100%;
        height: auto;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
    }
    .getallblog .real h1{
        font-size: 1.6rem;
        font-weight: 500;
        height: 8vh;
    }
    .getallblog .real .boxes{
        width: 100%;
        height:auto;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 2rem;
    }
    .getallblog .real .boxes .box{
        width: 90%;
        height: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .getallblog .real .boxes .box img{
        width: 100%;
        height: 50%;
        border-radius: 16px;
        box-shadow: 0 8px 30px rgb(0,0,0,0.22);
    }
    .getallblog .real .boxes .box h1{
        width: 100%;
        text-align: left;
        font-size: 1.33rem;
        font-weight: bold;
        opacity: 0.96;
        margin-bottom: 0.5rem;
    }
    .getallblog .real .boxes .box p{
        text-align: left;
        width: 100%;
        margin-top: 0.5rem;
        margin-bottom: 0.4rem;
    }
    .getallblog .real .boxes .box h2{
        font-size: 1.15rem;
        opacity: 0.7;
        text-align: left;
        width: 100%;
        margin-top: 0.4rem;
    }
}